import React, { Component } from "react";
import {
  NavLink,
} from "react-router-dom";

let value = 10;
let damage = 5;

class PayoffMatrix extends React.Component {
  render() {
    return <h1>Hello, {this.props.name}</h1>;
  }
}

const element = <PayoffMatrix name="Sara" />;

class Hawkdove extends Component {
  render() {
    return (
      <div>
        <h2>The Hawk Dove Game</h2>
        <p>
          Imagine you are a bird searching for food and you find a fruit on the ground.
          However, another bird reaches the food at the same time as you. 
        </p>
        <p>
          Do you fight with the other bird for sole ownership of the food 
          or try peacefully share it?
          The answer to this question is unclear since we cannot quantify utility and risk,
          but let us add some concrete values for context.
        </p>

        <p>
          Suppose that the value of the fruit is v = {value} and that the cost of losing a fight
          is d = {damage}. If you choose to fight and the other person chooses to negotiate,
          you can aggressively claim the value of fruit for yourself at no cost. 
          If you both choose to negotiate,
          the value of the fruit will be shared equally among the two of you. Lastly, if you 
          both choose to fight, you both have an equal chance of winning the entire value of 
          of the fruit, but also an equal change of losing the fight and taking a 
          damage penalty for losing. We can model
          this through a payoff matrix that shows the outcomes of each matchup below:
        </p>

        {element}
        
        <p>
          Now that we understand the outcomes of these payoffs, how can we determine the ideal
          strategy that you should take? Should you play the fighting (Hawk) strategy?
          Should you plays the peaceful (Dove) strategy? Or is there some ideal proportion of the 
          two strategies that you should employ? The optimal strategy is one that is likely
          to have the maximum expected payoff over many iterations of this game, regardless
          of whatever strategy the opponent uses. This strategy, (which you should use!)
          is known as an evolutionary stable strategy.
        </p>

        <button><NavLink to="/hawkdovesimulation">Next</NavLink></button>
      </div>
    );
  }
}
 
export default Hawkdove;